body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body, button, input, select, textarea {
  font-family: 'Lato', sans-serif;
}
.App {
  padding: 0;
  margin: 0;
}

fieldset {
  border: 1px solid transparent;
}
.section {
  padding: 1.5rem 1.5rem;
}

.table.is-transparent {
  background-color: transparent;
}

.LineChart {
  display: inline-block;
  position: relative;
  width: 100%;
  max-width: 960px;
  height: 500px;
  vertical-align: top;
}

.LineChart svg {
  display: inline-block;
  position: absolute;
  top: 0;
  left: 0;
}

.LineChart .data-line {
  fill: none;
  stroke: steelblue;
  stroke-width: 1.5px;
  -webkit-transition: -webkit-transform 0.5s;
  transition: -webkit-transform 0.5s;
  transition: transform 0.5s;
  transition: transform 0.5s, -webkit-transform 0.5s;
}

.LineChart .data-point {
  fill: steelblue;
  stroke: white;
  stroke-width: 2px;
  stroke-opacity: 0.5;
  -webkit-transition: stroke 0.5s, stroke-width 0.5s, stroke-opacity 0.5s;
  transition: stroke 0.5s, stroke-width 0.5s, stroke-opacity 0.5s;
}
.LineChart .data-point.is-success {
  fill: hsl(141, 71%, 48%);
}
.LineChart .data-point.is-danger {
  fill: hsl(348, 100%, 61%);
}
.LineChart .data-point.hover.is-success {
  stroke: hsl(141, 71%, 48%);
}
.LineChart .data-point.hover.is-danger {
  stroke: hsl(348, 100%, 61%);
}
.LineChart .data-point.hover {
  stroke-width: 12px;
  stroke-opacity: 0.5;
}

.LineChart .crosshair-line {
  stroke: #dbdbdb;
  stroke-width: 1px;
  stroke-dasharray: 4;
}

.LineChart .zoom {
  fill: none;
  pointer-events: all;
}

.LineChart .tooltip-container {
  position: relative;
  vertical-align: top;
  width: 100%;
}

.LineChart .tooltip {
  background-color: #ffffffe8;
  border: 1px solid #dbdbdb;
  border-radius: 5px;
  box-shadow: none;
  display: inline-block;
  font: 12px sans-serif;
  padding: 1.25rem;
  pointer-events: none;
  position: absolute;
  text-align: center;
  -webkit-transition: opacity 0.2s;
  transition: opacity 0.2s;
}


.bustabit-logo {
  display: inline-block;
}
.bustabit-logo:before {
  content: '';
  background-image: url(data:image/png;base64,);
  background-size: 100%;
  background-repeat: no-repeat;
  display: inline-block;
  position: relative;
  top: 4px;
  width: 12px;
  height: 12px;
  margin-right: 3px;
}
.bustabit-title {
  color: #e58929;
  font-family: 'Courier New';
  font-weight: 600;
  font-size: 0.6rem;
  line-height: 0.6rem;
  margin: 0;
  padding: 0;
  text-align: left;
  text-transform: lowercase;
}
.script-simulator-title {
  color: #363636;
  text-transform: uppercase;
  font-family: 'Lato', sans-serif;
  font-weight: 600;
  font-size: 1rem;
}

.script-simulator-author {
  color: #363636;
  text-transform: none;
  font-family: 'Lato', sans-serif;
  font-weight: 600;
  font-size: 0.6rem;
  line-height: 0.6rem;
  float: right;
}

.is-large .bustabit-logo:before {
  width: 20px;
  height: 20px;
}
.is-large .bustabit-title, .is-1 .script-simulator-author {
  font-size: 1rem;
  line-height: 1rem;
}
.is-large .script-simulator-title {
  font-size: 3rem;
}


